.my-home {
  position: relative;
  height: 100%;
  padding: 24px;
}
.my-home .article-list {
  width: 950px;
  padding: 16px;
  background-color: #fff;
  height: calc(100% - 16px);
  overflow: auto;
  border-radius: 10px 0 0 10px;
}
.my-home .article-list .visit-list {
  font-size: 12px;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 16px;
  min-height: 38px;
  overflow: hidden;
  background-color: #fff;
}
.my-home .article-list .visit-list span {
  color: #aaa;
}
.my-home .article-list .visit-list .int {
  display: inline-block;
  width: 10px;
}
.my-home .article-list .saohua {
  transition: 0.5s;
  font-size: 12px;
  color: #eee;
  font-family: 'cursive';
  margin: 10px 0;
  text-align: center;
}
.my-home .article-list .saohua:hover {
  transition: 0.5s;
  font-size: 18px;
  color: #3E87E3;
}
.my-home .article-list .list {
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  overflow: hidden;
  padding: 10px;
  margin-bottom: 16px;
  background-color: #fff;
}
.my-home .article-list .list .list-item {
  float: left;
  width: 50%;
  margin: 0;
}
.my-home .article-list .list .list-item a {
  color: #999;
  display: inline-block;
  max-width: 435px;
  vertical-align: middle;
}
.my-home .article-list .list .list-item a:hover {
  color: #333;
}
.my-home .article-list .list:hover {
  transition: 0.5s;
  box-shadow: 0 0 4px #3E87E3;
}
.my-home .side-bar {
  position: absolute;
  right: 24px;
  top: 24px;
  z-index: 2;
  width: 23%;
}
.my-home .side-bar .search {
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  overflow: hidden;
  height: 38px;
  line-height: 38px;
  background-color: #fff;
}
.my-home .side-bar .search .ant-input,
.my-home .side-bar .search .ant-input:focus,
.my-home .side-bar .search .ant-input:hover {
  border: none;
  box-shadow: none;
}
.my-home .side-bar .search:hover {
  transition: 0.5s;
  box-shadow: 0 0 4px #3E87E3;
}
.my-home .side-bar .personal {
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  padding: 10px;
  margin-top: 16px;
  background-color: #fff;
}
.my-home .side-bar .personal .ant-descriptions-title {
  margin-bottom: 5px;
}
.my-home .side-bar .personal .avatar-user {
  display: flex;
  align-items: center;
}
.my-home .side-bar .personal .avatar-user .username {
  margin-left: 10px;
}
.my-home .side-bar .statistics {
  transition: 0.5s;
  box-shadow: 0 0 4px #ddd;
  border-radius: 10px;
  padding: 10px;
  margin-top: 16px;
  text-align: center;
  background-color: #fff;
}
